const app = getApp();
const common = require('../../../publicjs/common.js');
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uniIcons from '@/components/uni-icons/uni-icons.vue';
import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue';
import uniSwipeActionItem from '@/components/uni-swipe-action-item/uni-swipe-action-item.vue';
export default {
	components: {
		uniNavBar,
		uniIcons,
		uniSwipeAction,
		uniSwipeActionItem
	},
	data() {
		return {
			options1: [
				{
					text: '删除',
					style: {
						backgroundColor: "#4C6FF3",
						fontSize: '26rpx',
						btnHeight: '140rpx',
						borderRadius: '0 15rpx 15rpx 0'
					}
				}
			],
			pagesurl: '',
		};
	},
	onLoad(option) {
		if(option !== "" && typeof option.pagesurl != "") {
			let param = JSON.parse(option.item);
			this.pagesurl = param.pagesurl;
		}
	},
	onShow() {
		this.userinfo();
	},
	watch: {
		pagesurl(newVal, val) {
			this.pagesurl = newVal;
		}
	},
	methods: {
		userinfo() {},
		//滑块
		bindClick(e) {
			uni.showToast({
				title: `点击了${e.content.text}按钮`,
				icon: 'none'
			});
		},
		
		//点击进入通知详情
		goinformdetails() {
			let params= {
				pagesurl: "/pages/userCentre/inform/inform"
			}
			params = JSON.stringify(params)
			uni.navigateTo({
				url: './infomdetails?item=' + params
			})
		}
	}
};
